<template>
  <main class="new-estate">
    <section class="page-title-section theme-bg dashboard-title">
      <div class="container dashboard-modified"></div>
    </section>
    <div class="container mt-3 min-80vh">
      <form-wizard
        title="Register New Estate"
        subtitle="Please fill the fields carefully"
        color="#193870"
        errorColor="#ef476f"
        @on-loading="setLoading"
        @on-complete="onComplete"
      >
        <tab-content title="Estate Information" :before-change="validateAsync" icon="ti-clipboard">
          <div class="row justify-content-center">
            <div class="col-md-5 mb-2">
              <input
                type="text"
                class="form-control"
                v-model="estateData.estateName"
                placeholder="Estate Name"
              >
            </div>
            <div class="clearfix"></div>
            <div class="invalid-feedback block text-center">{{errors.estateName}}</div>
          </div>
          <div class="row justify-content-center">
            <div class="col-md-5 mb-2">
              <input
                type="text"
                class="form-control"
                v-model="estateData.companyName"
                placeholder="Company Name"
              >
            </div>
            <div class="clearfix"></div>
            <div class="invalid-feedback block text-center">{{errors.companyName}}</div>
          </div>
          <div class="row justify-content-center">
            <div class="col-md-5 mb-2">
              <input
                type="text"
                class="form-control"
                v-model="estateData.address"
                placeholder="Address"
              >
            </div>
            <div class="clearfix"></div>
            <div class="invalid-feedback block text-center">{{errors.address}}</div>
          </div>
          <div class="row justify-content-center">
            <div class="col-md-5 mb-2">
              <input
                type="text"
                class="form-control"
                v-model="estateData.state"
                placeholder="State"
              >
              <div class="clearfix"></div>
              <div class="invalid-feedback block text-center">{{errors.state}}</div>
            </div>
          </div>
        </tab-content>
        <tab-content title="Utility Setup" icon="ti-money">
          <div class="row justify-content-center">
            <div class="col-md-5 mb-2">
              <label for="waterUnitRate">Water Rate N/Cm3</label>
              <input
                type="text"
                v-model="estateData.waterUnitRate"
                id="waterUnitRate"
                class="form-control"
                placeholder="0"
              >
            </div>
          </div>
          <div class="row justify-content-center">
            <div class="col-md-5 mb-2">
              <label for="electricityUnitRate">Electricty Rate N/KWH</label>
              <input
                type="text"
                v-model="estateData.electricityUnitRate"
                id="electricityUnitRate"
                class="form-control"
                placeholder="0"
              >
            </div>
          </div>
        </tab-content>
        <tab-content title="Other Billable Services" :before-change="validateAsync" icon="ti-more">
          <div class="row">
            <div class="col-md-12 mb-2">
              <h5 class="dark-color text-center">Add Service charges</h5>
              <p class="text-center mb-3">Eg- Gardening, Parking, Sewage Disposal etc.</p>

              <div class="row justify-content-center">
                <div class="col-md-6">
            
                  <div
                    class="row"
                    v-for="(service, index) in estateData.otherBillableServices"
                    v-bind:key="index"
                  >
                    <div class="col-md-5 mb-1">
                      <select
                        class="form-control"
                        placeholder="Choose service name"
                        v-model="service.serviceName"
                      >
                        <option :value="null" disabled>Select service charge</option>
                        <option>Lump sum</option>
                        <option>Gardening</option>
                        <option>Parking</option>
                        <option>Sewage</option>
                      </select>
                    </div>
                    <div class="col-md-5">
                      <input
                        type="text"
                        placeholder="Amount"
                        class="form-control"
                        v-model="service.serviceAmount"
                      >
                    </div>
                    <div class="col-md-2">
                      <button class="btn btn-primary btn-sm" v-on:click="addBillableService">
                        <span class="ti-save"></span>
                      </button>
                    </div>
                  </div>
                </div>
              </div>

              <div class="row text-center">
                <div class="col-md-12">
                  <button class="btn btn-primary btn-sm mt-3" v-on:click="addBillableService">
                    <span class="ti-plus"></span> Add new service
                  </button>
                </div>
              </div>
            </div>
          </div>
        </tab-content>
      </form-wizard>
    </div>
  </main>
</template>

<script>
import { FormWizard, TabContent } from "vue-form-wizard";
import "../../assets/static/css/form_wizard.css";
import * as auth from "../../services/auth_service";
import * as estate from "../../services/estate_service";


export default {
  name: "newEstate",
  components: {
    FormWizard,
    TabContent
  },
  data() {
    return {
      estateData: {
        loadingWizard: false,
        estateName: "",
        companyName: "",
        address: "",
        state: "",
        waterUnitRate: 80,
        electricityUnitRate: "",
        otherBillableServices: []
      },
      service: {
        serviceName: ""
      },
      errors: []
    };
  },
  methods: {
    addBillableService: function() {
      this.estateData.otherBillableServices.push({
        serviceName: "",
        serviceAmount: ""
      });
    },
    setLoading: function(value) {
      this.loadingWizard = value;
    },
    validateAsync: function() {
      return new Promise((resolve, reject) => {
        if (this.estateData.estateName.length < 1) {
          reject(true);
        } else {
          resolve(true);
        }
      });
    },

    onComplete: async function() {
      this.addBillableService();
      this.errors = [];
      if (this.estateData.estateName.length < 2) {
        this.errors.estateName = "Please name of estate";

        return;
      }

      try {
        //console.log("View Message : Saved!" + this.estateData.estateName)
        const { data } = await estate.storeEstateData(this.estateData);
        this.flashMessage.success({
          title: "New estate added",
          message: this.estateData.estateName + " has been saved!",
          time: 5000,
          icon: "../../assets/static/img/water_drop.svg"
        });
         this.$router.push({name:'estates'})
       
      } catch (error) {
        this.flashMessage.error({
          title: "There was a problem saving the estate",
          message: "Kindly check your estate name!",
          time: 5000,
          icon: "../../assets/static/img/water_drop.svg"
        });
      }
    }
  }
};
</script>

